import { Session } from './../../../services/session';
import { UserProvider } from './../../../providers/user-provider';
import { workflow_tab1Root, workflow_tab2Root, workflow_tab3Root, workflow_tab4Root, workflow_tabs } from './../../pages';
import { Events } from 'ionic-angular';
import { ViewController } from 'ionic-angular';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';
/**
 * Generated class for the WorkflowPage page.
 *
  WorkflowPage 不能修改，若要修改，请在app.component.ts也修改相应关键词
 */

@IonicPage()
@Component({
  selector: 'page-workflow',
  templateUrl: 'workflow.html',
})
export class WorkflowPage {

  workflow_tab1Root = workflow_tab1Root;
  workflow_tab2Root = workflow_tab2Root;
  workflow_tab3Root = workflow_tab3Root;
  workflow_tab4Root = workflow_tab4Root;

  tab1Title = " ";
  tab2Title = " ";
  tab3Title = " ";
  tab4Title = " ";

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public translateService: TranslateService,
    public events: Events) {

    
    this.events.subscribe('back-to-home', () => {
      this.viewCtrl.dismiss()
    });

    this.setTabText();

  }
  setTabText() {
    this.translateService.get(['WORKFLOW_TAB1_TITLE', 'WORKFLOW_TAB2_TITLE', 'WORKFLOW_TAB3_TITLE', 'WORKFLOW_TAB4_TITLE']).subscribe(values => {
      this.tab1Title = values['WORKFLOW_TAB1_TITLE'];
      this.tab2Title = values['WORKFLOW_TAB2_TITLE'];
      this.tab3Title = values['WORKFLOW_TAB3_TITLE'];
      this.tab4Title = values['WORKFLOW_TAB4_TITLE'];
    });
  }

  ionViewDidLoad() {

  }

}



